<template lang="">
  <div>
    <label>
      <input
        type="checkbox"
        true-value="yes"
        false-value="no"
        v-model="checked"
      />
      {{ checked }}
    </label>
  </div>
</template>
<script>
export default {
  data() {
    return {
      checked: true,
    };
  },
};
</script>
<style lang=""></style>
